import { Component, OnInit } from '@angular/core';
import { SellerserviceService } from 'src/app/services/sellerservice.service';
import { Carbuyer } from 'src/app/models/carbuyer';


@Component({
  selector: 'app-seller',
  templateUrl: './seller.component.html',
  styleUrls: ['./seller.component.css']
})
export class SellerComponent implements OnInit {
  carBuyers: Carbuyer[];

  constructor(private sellerSvc: SellerserviceService) { }

  ngOnInit() {
    this.sellerSvc.getCar()
      .subscribe(data => {
        this.carBuyers = data;
      });

  }

}
