import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import{Carbuyer} from '../models/carbuyer'


@Injectable({
  providedIn: 'root'
})
export class SellerserviceService {
  private baseUrl: string = 'http://localhost:56604/api/CarDetails';

  constructor(private http: HttpClient) { }

  getCar() {
    return this.http.get<Carbuyer[]>(this.baseUrl);
  }

}
