import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http'
import { Carbuyer } from '../models/carbuyer';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class BuyerserviceService {
  // private uri = 'http://localhost:62140/api/Buyers';
  private uri = "http://137.116.149.189/api/Buyers";

  constructor(private http: HttpClient) { }

  showBuyers() {
    return this.http.get<Carbuyer[]>(this.uri)
  }


}
