import { Component, OnInit } from '@angular/core';
import { BuyerserviceService } from 'src/app/services/buyerservice.service';
import { Carbuyer } from 'src/app/models/carbuyer';

@Component({
  selector: 'app-carbuyer',
  templateUrl: './carbuyer.component.html',
  styleUrls: ['./carbuyer.component.css']
})
export class CarbuyerComponent implements OnInit {
  carBuyers: Carbuyer[];
  constructor(private buyerSvc:BuyerserviceService) { }

  ngOnInit() {
    this.buyerSvc.showBuyers()
      .subscribe(data => {
        this.carBuyers = data;
      });
  }

}
